.orderRacks {
  .myAccordion {
    .step {
      .header {
        cursor: pointer;
        font-size: 1.5rem;
        line-height: 1.2;

        .type {
          white-space: nowrap;
        }

        .value {
          flex-grow: 1;
        }

        .price {
          font-weight: 300;
          margin-left: 0.5em;
        }
      }

      .content {
        align-content: center;
        align-items: flex-start;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-evenly;

        .figure {
          cursor: pointer;

          .price {
            background: #49505780;
            border-top-left-radius: 50%;
            color: #fff;
            display: block;
            float: right;
            font-weight: bold;
            height: 2.3em;
            line-height: 2.3em;
            margin-top: -2em;
            margin: -2.6em 0.3em 0;
            padding: 0 0.5em;
            position: relative;
            text-align: center;
          }
        }
      }

      &.price {
        text-align: right;

        .header {
          cursor: default;

          .type {
            font-weight: 600;
          }
        }
      }
    }
  }

  .configureRack {
    margin-bottom: 1.5em;

    .step {
      &.machine {
        .figure {
          flex: 40% 0 1;
        }
      }

      &.capacity {
        .figure {
          flex: 28% 0 1;
        }
      }

      &.color {
        .figure {
          flex: 75px 1 0;
          margin: 0.5em 0.8em;
        }
      }

      @media (min-width: 992px) {
        &.color {
          .figure {
            flex-basis: 110px;
          }
        }
      }
    }
  }

  .configureShippingPayment {
    .step {

      &.payment {
        label {
          flex: 30% 0 0;

          .price {
            flex-grow: 1;
          }

          .details {
            font-size: 0.8em;
          }
        }
      }
    }
  }

  .validate {
    .waiting {
      height: 1.2em;
      margin-right: 0.5em;
      width: 1.2em;
    }
  }
}
